.game-board {
  margin: 0 auto;
  border: 1px solid white;
  width: 500px;
  height: 500px;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-auto-rows: repeat(10, 1fr);
  gap: 1px;
  padding: 1px;
}
.block {
  position: relative;
}
.btn {
  border: 1px solid white;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn svg {
  width: 100%;
  height: 100%;
  background-color: transparent;
}
.ship {
  background-color: rgb(0, 0, 145);
}
.btn:hover {
  cursor: pointer;
}
.miss {
  background-color: black;
}
.miss:hover {
  cursor: default !important;
}
.hit {
  background-color: rgba(0, 128, 0, 0.719);
}
.hit:hover {
  cursor: default !important;
}
.sunk {
  background-color: red;
}
.sunk:hover {
  cursor: default !important;
}
.preview {
  background-color: rgb(113, 113, 151);
}
@media only screen and (max-width: 600px) {
  .game-board {
    height: 400px;
    width: 400px;
  }
}
@media only screen and (min-width: 600px) {
}
@media only screen and (min-width: 768px) {
  .game-board {
    height: 350px;
    width: 350px;
  }
}
@media only screen and (min-width: 992px) {
  .game-board {
    height: 420px;
    width: 420px;
  }
}
@media only screen and (min-width: 1200px) {
  .game-board {
    height: 500px;
    width: 500px;
  }
}
